import React from 'react';
import { NavBar, Icon } from 'antd-mobile';
import Mytab from './Mytab'
import {connect} from 'react-redux'
const Cart=(props)=>{
    return <div>
        <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={() =>props.history.goBack()}
            rightContent={[
                <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                <Icon key="1" type="ellipsis" />,
             ]}
        >收藏</NavBar>
        <div>
            {props.mydata.shouCang.map((item,index)=>{
                return <div key={index} style={{border:'1px solid #ccc',height:'100px'}}>
                    <img style={{width:'100px',height:'100px',float:'left'}} src={item}/>
                    <button style={{height:'50px',width:'100px',marginLeft:'130px',marginTop:'25px'}} onClick={()=>{
                        props.dispatch({
                            type:'DELETE',
                            value:index
                        })
                    }}>取消收藏</button>
                </div>
            })}
        </div>
        <Mytab/>
    </div>
}
const mapStateToProps=(state)=>{
    return{
        mydata:state
    }
}
export default connect(mapStateToProps)(Cart);