import React ,{useEffect} from 'react';
import {SearchBar,Carousel, Button} from 'antd-mobile'
import './home.css'
import Mytab from './Mytab'
import {connect} from 'react-redux'
import {getgoodslist} from './actionCreaters';
const Home=(props)=> {
    // console.log(props);
    useEffect(() => {
        props.dispatch(getgoodslist());
    },[]);
    function gl(){
        if(props.mydata){
            return props.mydata.map((item,index)=>
                <div key={index} className="img" onClick={()=>props.history.push('/detail',{src:item.img})}>
                    <img alt='img' src={item.img}/>
                    <p>{item.title}</p>
                    <p id='id'><span>￥  {item.price} </span>19人已购买</p>
                </div>)  
        }
        else return
    }
    const imgs=[
        'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
        'https://gw.alicdn.com/imgextra/i1/48/O1CN01LedM5R1CE1dm14g6n_!!48-0-lubanu.jpg',
        'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
        'https://gw.alicdn.com/imgextra/i1/24/O1CN012V88lQ1C328R02BAB_!!24-0-ppp-picassogw.jpg',
        'https://picasso.alicdn.com/imgextra/i3/382208/O1CNA1iyU1IP100ff4af62860b14de730004_!!382208-0-picassobanner.jpg'

    ];
    return (<div>
        <SearchBar placeholder="寻找宝贝店铺" maxLength={8} onFocus={()=>props.history.push('/search')}/>
        <Carousel autoplay infinite>
            {
                imgs.map((item,idx)=><a key={idx}
                    style={{ display: 'inline-block', width: '100%',height:'120px'}}>
                        <img src={`${item}`} alt='' 
                         style={{ width: '100%', heigth:'120px'}}
                        />
                    </a>)
            }
        </Carousel>
        <div className="like">
            <img alt='like' src='https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png'/>
        </div>
        {gl()}
        <Mytab/>
    </div>)
  }
const mapStateToProps=state=>{
    return{
        mydata:state.getgoodslist.datalist
    }   
}

export default connect(mapStateToProps)(Home)