import React, { Component } from 'react'
import { NavBar, Icon } from 'antd-mobile';
import {TabBar} from 'antd-mobile'
import {connect} from 'react-redux'
class Detail extends React.Component{
    constructor(props){
      super(props);
      this.state={
        selectedTab:'/detail/dianpu'
      }
    }
    render() {
      return(
        <div>
        <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={() =>this.props.history.goBack()}
            rightContent={[
                <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                <Icon key="1" type="ellipsis"/>,
            ]}
        >详情</NavBar>
        <div>
            <img style={{width:'374px',height:'374px'}} src={this.props.location.state.src}/>
        </div>
        <div style={{position: 'fixed', width: '100%', bottom:0}}>
              <TabBar
                unselectedTintColor="#949494"
                tintColor="#33A3F4"
                barTintColor="white"
                >
                <TabBar.Item
                  title="店铺"
                  icon={
                    <i className='iconfont icon-dianpu'></i>
                  }
                  selectedIcon={
                    <i className='iconfont icon-dianpu'></i>
                  }
                  // selected={this.state.selectedTab === '/detail/dianpu'}
                  onPress={()=>{
                    this.setState({
                      selectedTab: '/detail/dianpu',
                    });
                  }}
                >
                </TabBar.Item>
                <TabBar.Item
                  icon={
                    <i className='iconfont icon-kefu'></i>
                  }
                  selectedIcon={
                    <i className='iconfont icon-kefu'></i>
                  }
                  title="客服"
                  onPress={()=>{
                    this.setState({
                      selectedTab: '/detail/kefu',
                    });
                  }}
                  // selected={this.state.selectedTab === '/detail/kefu'}
                  >
                </TabBar.Item>
                <TabBar.Item
                  icon={
                    <i className='iconfont icon-shoucang' ></i>
                  }
                  selectedIcon={
                    <i className='iconfont icon-shoucang' ></i>
                  }
                  title="收藏"
                  selected={this.props.mydata.shouCang.some(item=>{return this.props.location.state.src==item})}
                  onPress={()=>{
                    if(!this.props.mydata.Login){
                      this.props.history.push('/login')
                    }else{
                    this.setState({
                      selectedTab: '/detail/shoucang',
                    });
                    this.props.dispatch({
                      type:'SHOUCANG',
                      value:this.props.location.state.src
                    })
                    }}
                  }
                >
                </TabBar.Item>
                <TabBar.Item
                  icon={
                    <div style={{width:"100px",height:"35px",color:"white",lineHeight:"35px",
                    borderTopLeftRadius :"40px",borderBottomLeftRadius :"40px",
                    backgroundImage: "linear-gradient(to right, #FFC500, #FF9402)"}}>加入购物车</div>
                  }
                >
                </TabBar.Item>
                <TabBar.Item
                  icon={
                    <div style={{width:"100px",height:"35px",color:"white",lineHeight:"35px",
                    borderTopRightRadius :"40px",borderBottomRightRadius :"40px",
                    backgroundImage: "linear-gradient(to right, #FF7A00, #FE560A)"}}>立即购买</div>
                  }
                >
                </TabBar.Item>
              </TabBar>
            </div>
        </div>
      )
    }
  }
const mapStateToProps=state=>{
  return {
    mydata:state
  }
}
export default connect(mapStateToProps)(Detail);