let shoucang=[],
    userinfo='',
    datalist=[];
function getgoodslist(state=datalist,action){
    switch (action.type){
        case 'GET_GOODSLIST':
            return {datalist:action.datalist};
        default:
            return state
    }
}
function shouCang(state=shoucang,action){
    switch (action.type){
        case 'SHOUCANG':
            return [...state,action.value];
        case 'DELETE':
            let arr=[...state];
            arr.splice(action.value,1);
            return arr;
        default:
            return state
    }
}

function Login(state=userinfo,action){
    switch (action.type){
        case 'LOGIN':
            return {userinfo:action.value}
        default:
            return state
    }
}
export {getgoodslist}
export {shouCang}
export {Login}