import React from 'react';
import {TabBar} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'

class Mytab extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        selectedTab: '/home',
      };
    }
  
    render() {
      const {pathname}=this.props.location;
      return (
        <div style={{position: 'fixed', width: '100%', bottom:0}}>
          <TabBar
            unselectedTintColor="#949494"
            tintColor="#33A3F4"
            barTintColor="white"
            >
            <TabBar.Item
              title="首页"
              key="home"
              icon={
                <i className='iconfont icon-Homehomepagemenu'></i>
              }
              selectedIcon={
                <i className='iconfont icon-Homehomepagemenu1'></i>
              }
              selected={pathname === '/home'}
              onPress={() => {
                  this.props.history.push('/home');
              }}
            >
            </TabBar.Item>
            <TabBar.Item
               icon={
                <i className='iconfont icon-shoucang' ></i>
              }
              selectedIcon={
                <i className='iconfont icon-shoucang' ></i>
              }
              title="收藏"
              key="cart"
              selected={pathname === '/cart'}
              onPress={() => {
                this.props.mydata?this.props.history.push('/cart'):this.props.history.push('/login')
              }}
              >
            </TabBar.Item>
            <TabBar.Item
              icon={
                <i className='iconfont icon-dingdan'></i>
              }
              selectedIcon={
                <i className='iconfont icon-dingdan'></i>
              }
              title="订单"
              key="dingdan"
              selected={pathname==='/dingdan'}
              onPress={() => {
                this.props.history.push('/dingdan')
             }}
            >
            </TabBar.Item>
            <TabBar.Item
              icon={
                <i className='iconfont icon-wode'></i>
              }
              selectedIcon={
                <i className='iconfont icon-wodedangxuan'></i>
              }
              title="我的淘宝"
              key="my"
              selected={pathname==='/my'}
              onPress={() => {
                 this.props.history.push('/my')
              }}
            >
            </TabBar.Item>
            <TabBar.Item
              icon={
                <i className='iconfont icon-gengduo'></i>
              }
              selectedIcon={
                <i className='iconfont icon-19'></i>
              }
              title="更多"
              key="more"
              selected={pathname === '/more'}
              onPress={() => {
                 this.props.history.push('/more')
              }}
            >
            </TabBar.Item>
          </TabBar>
        </div>
      );
    }
  }
const mapStateToProps=state=>{
  return {mydata:state.Login}
}
export default connect(mapStateToProps)(withRouter(Mytab));