import React from 'react'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import { NavBar, Icon ,Button} from 'antd-mobile'
const Login=(props)=>{
    return <div>
        <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={() =>props.history.goBack()}
            rightContent={[
                <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                <Icon key="1" type="ellipsis" />]}
        >登录</NavBar>

        <div style={{marginTop:'100px',marginLeft:'50px'}}>
        <input type='text' name='username' placeholder='手机号/邮箱/会员名' style={{width:'280px',height:'30px'}}/><br/>
        <input type='password' name='pwd' placeholder='请输入登录密码' style={{width:'280px',height:'30px',marginTop:'20px',marginBottom:'10px'}}/><br/>
        <Link to=''>短信验证码登录</Link><Link to='' style={{marginLeft:'130px'}}>免费注册</Link>
        <Button type='primary' style={{width:'280px',marginTop:'20px'}} onClick={()=>{
            props.dispatch({type:'LOGIN',value:'张三'})
            props.history.push('/home')
        }}>登录</Button>
        </div>
    </div>
}
const mapStateToProps=state=>{
    return {
        mydata:state.Login
    }
}
export default connect(mapStateToProps)(Login);